<template>
  <page-layout>
    <div class="mdl-tabs mdl-js-tabs mdl-js-ripple-effect">
      <div class="mdl-tabs__tab-bar mdl-cell--bottom">
        <a href="#procurements" class="mdl-tabs__tab is-active">Procurements</a>
        <a href="#sales" class="mdl-tabs__tab">Sales</a>
      </div>
      <div class="mdl-tabs__panel is-active" id="procurements">
        <procurement-list-view></procurement-list-view>
      </div>
      <div class="mdl-tabs__panel" id="sales">
        <sales-list-view></sales-list-view>
      </div>
    </div>
  </page-layout>
</template>
<script>
import { PageLayout } from 'LAYOUTS_COMPONENTS'
import { ProcurementListView } from '@/apps/procurements/components'
import { SalesListView } from '.'
export default {
  name: 'sales-view',
  components: {
    PageLayout,
    ProcurementListView,
    SalesListView
  }
}
</script>



